<template>
  <section class="container-xl p-sm-3 p-md-4">
    <wired-card elevation="5" class="px-sm-4 py-sm-3 px-md-5 py-md-4 w-100" v-if="fullWidth >= 769">
      <router-view />
    </wired-card>
    <router-view v-else />
  </section>
  <div class="paperOverlay"></div>
  <Loading />
  <EditModal />
  <MessageModal />
</template>

<style lang="scss">
@import '@/assets/stylesheets/all';
</style>

<script>
import 'wired-elements';
import Loading from '@/components/models/LoadingModel.vue';
import EditModal from '@/components/models/EditModal.vue';
import MessageModal from '@/components/models/MessageModal.vue';

export default {
  inheritAttrs: false, // 移除不必要警告
  data() {
    return {
      fullWidth: 0,
    };
  },
  components: {
    Loading,
    EditModal,
    MessageModal,
  },
  mounted() {
    this.fullWidth = window.innerWidth; // 動態取得瀏覽器寬度
    window.onresize = () => {
      this.fullWidth = window.innerWidth;
    };
  },
};
</script>
